@import url(https://fonts.googleapis.com/css?family=Tajawal&display=swap);
:root {
  --bg-color: #1c1c1f;
  --primary-color: #e5e5e5;
  --primary-red: #b30000;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 62.5%;
  font-family: 'Tajawal', sans-serif;
  line-height: 1.7;
  background-color: #1c1c1f;
  background-color: var(--bg-color);
  color: #e5e5e5;
  color: var(--primary-color);
}

.loader,
.loader:after {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}
.loader {
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  border-top: 1.1em solid rgba(201, 76, 76, 0.2);
  border-right: 1.1em solid rgba(201, 76, 76, 0.2);
  border-bottom: 1.1em solid rgba(201, 76, 76, 0.2);
  border-left: 1.1em solid #b30000;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.Map {
  height: 100vh;
  max-width: 100%;
  font-size: 0.9rem;
  font-weight: bold;
  padding-top: 3rem;
}

.navbar {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  background-color: var(--bg-color);
  height: 4rem;
  z-index: 100;
}

.bg-shadow {
  box-shadow: 0px 10px 5px 0px rgba(0, 0, 0, 0.75);
  -webkit-transition: box-shadow 0.3s ease-out;
  transition: box-shadow 0.3s ease-out;
}

.navbar ul {
  list-style: none;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
}

.navbar ul li {
  padding: 1rem 1rem;
  font-size: 1.4rem;
  font-weight: 200;
}
.navbar ul li a {
  text-decoration: none;
  color: var(--primary-color);
  cursor: pointer;
}

.navbar ul li a::after {
  content: '';
  display: block;
  width: 0;
  height: 2px;
  background-color: var(--primary-red);
  -webkit-transition: width 0.3s;
  transition: width 0.3s;
}

.navbar ul li a:hover::after {
  width: 100%;
}

.navbar ul li a.active::after {
  content: '';
  display: block;
  width: 100%;
  height: 2px;
  background: var(--primary-red);
}

.country-select {
  position: absolute;
  top: 6rem;
  right: 2rem;
  color: #0e0e0f;
  background-color: #0e0e0f;
}

.flag-select__btn {
  color: var(--primary-color);
  outline: none;
}

.flag-select__options {
  right: -1.5rem;
  width: 15rem;
}

.global {
  text-align: center;
  margin-top: 12rem;
}

.img-size {
  width: 15rem;
  height: 10rem;
}

.global .container {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-around;
  padding-top: 3rem;
  align-items: center;
}

.global .container h2 {
  font-size: 2rem;
  font-weight: 200;
}

.number-circle {
  display: inline-block;
  border-radius: 50%;

  padding: 1.5rem;
  padding-bottom: 4rem;
  margin-bottom: 3rem;
  color: #666;
  text-align: center;

  font: 1.8rem Arial, sans-serif;
}

.confirmed {
  /* border: 2px solid #fe5e51; */
  color: #fe5e51;
}

.recovered {
  /* border: 2px solid #4ca64c; */
  color: #4ca64c;
}

.deaths {
  /* border: 2px solid var(--primary-red); */
  color: #ff3232;
}

@media (max-width: 655px) {
  .global .container {
    flex-direction: column;
  }
  .global .container div h2 {
    padding-top: 1rem;
    margin-bottom: 0.5rem;
  }

  .number-circle {
    margin-bottom: 2rem;
    padding-bottom: 2rem;
  }
  .global img {
    padding-left: 2rem;
  }
}

footer a {
  text-decoration: none;
  color: var(--primary-color);
  font-size: 1.3rem;
  padding-top: 2rem;
}

footer div {
  font-size: 0.6rem;
}
footer div a {
  font-size: 0.6rem;
}

footer {
  margin-top: 3rem;
  position: static;
  bottom: 0;
  width: 100%;
  text-align: center;
  color: var(--primary-color);
  padding-top: 1rem;
  height: 4.5rem;
}

